@import url(https://fonts.googleapis.com/css?family=Alegreya:700 |Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
html, body {
  margin: 0;
  height: 100%;
  min-height: 100%;
  position: relative;
}

body {
  position: relative;
}

div#root {
  margin: 0;
  height: 100%;
  min-height: 100%;
  position: relative;
}

div.App {
  margin: 0;
  height: 100%;
  overflow: auto;
  min-height: 100%;
  position: relative;
}

#appTitle {
  display: inline-block;
  margin-top: 10px;
  margin-left: 7.5%;
  width: 85%;
  text-align: center;
  font-family: "Alegreya", serif;
  font-weight: 700;
}

#title-refresh-div {
  display: inline-block;
}

#refresh {
  border-radius: 5px;
  height: 32px;
  margin-left: -20px;
}

div.jumbotron {
  margin: auto;
  position: relative;
  padding: 0px;
  height: 40%;
  min-height: 40%;
  background-color: white;
  border: 7px solid grey;
  border-radius: 0px;
}

div.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 220px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 0px;
  padding-left: 0px;
}



.form-inline {
  width: 100%;
  margin: auto;
}

#searchBarContainer {
  margin-left: auto;
  margin-right: auto;
  width: 75%;
}

#searchBar {
  width: 100%;
  padding: 0;
  text-align: center;
  margin-top: -20px;
}


#searchButton {
  margin-left: auto;
  margin-right: auto;
  height: 30px;
  width: 50%;
  margin-top: -11px;
  margin-bottom: -10px;
  padding: 0px;
  background-color: #378dea;
  border-color: #378dea;
}

#headerPic {
  display: block;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

/* Results Header*/

#greetingCard {
  margin:auto;
  text-align: center;
  font-family: "Alegreya" , serif;
  font-weight: 700;
  border: 7px solid grey;
  background-color: white;
}

h5#greetingBody {
  padding: 5px;
  font-family: "Alegreya" , serif;
  font-weight: 700;
  margin-top: 5px;
}


/********* Results Forecast */
/********* Results Forecast */
/********* Results Forecast */

div#forecast {
  margin: auto;
  width: 80%;
  font-family: "Alegreya" , serif;
  font-weight: 700;
  background-color: white;
  border: 10px solid grey;
}

.h5Forecast {
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  color: black;
  font-family: "Alegreya" , serif;
  font-weight: 700;
  text-align: center;

}

#resultsContainer {
  height: 50%; 
  min-height: 50%;
  position: relative;
  -webkit-animation-delay: 1000ms;
          animation-delay: 1000ms;
}

/********** Nav Bar */
/********** Nav Bar */
/********** Nav Bar */

#mainNav {
  display: inline;
  position: relative;
}

#nav-container {
  position: relative;
  height: 75px;
}

#mainNav ul {
  margin-top: -5px;
  margin-left:  auto;
  margin-right:  auto;
  margin-bottom: 10px;
  width: 95%;
  text-align: center;
  padding: 0px;
}

#form-div {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5px;
  width: 100%;
}

#favorite-options {
  text-align-last: center;
  font-size: 16px;
  height: 35px;
  width: 45%;
  float: left;
  background-color: #378dea;
  border-color: #378dea;
  margin-left: 5px;
}

input.edit-nav {
  display: block;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 45%;
  height: 35px;
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

#mainNav ul a {
  background: grey;
  color: black;
  border-radius: 3px;
  padding: 2px;
  font-family: "Alegreya" , serif;
  font-weight: 700;
  text-align: center;
}


#mainNav li {
  display: inline-block;
  margin-right: 15px;
}

#mainNav .active {
  background-color: #26942e;
  text-decoration: none;
}



/********* Forecast Flex Container*/
/********* Forecast Flex Container*/
/********* Forecast Flex Container*/



#conditions {
  background-color: #f1f1f1;
  margin: auto;
  text-align: center;
  position: relative;
}

#conditions-header {
  font-family: "Alegreya", serif;
  font-weight: 700;
}

#conditions-desc {
  font-family: "Alegreya", serif;
  font-weight: 700;
  margin-top: -5px;
}

#forecast-container {
  height: 100%;
  margin-top: auto;
  margin-bottom: auto;
}
#forecast-div {
  margin: auto;
  height: 85%;
}
#forecast-flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 90%;
}

#forecast-flex div {
  font-family: "Alegreya", serif;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f1f1f1;
  width: 85px;
  height: 85px;
  margin: 10px;
  text-align: center;
  line-height: 25px;
  font-size: 14px;
  padding: 5px;
  box-shadow: -2.5px -2.5px 1px #ded6d6;
}

.p-header {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  margin-top: 10px;
  -webkit-margin-after: 5px;
          margin-block-end: 5px;
  margin-bottom: 15px;
  line-height: 1.25;
}

.p-detail {
  margin-top: -7.5px;
  line-height: 1.25;
}

/* #wind-div {

} */
.wind-header {
  -webkit-margin-before: -5px;
          margin-block-start: -5px;
  line-height: 1.25;
  margin-top: 2.5px;
  margin-bottom: 0px;
}

.wind-detail {
  line-height: 1;
  -webkit-margin-after: 0px;
          margin-block-end: 0px;
}

#forecast-flex div.tide {
  flex-direction: column;
  font-size: 14px;
  padding: 0;
  line-height:18px
}

div.tide p {
  margin-top: 0px;
  margin-bottom: 0px;
}

/* FiveDay  */

#fiveDayContainer {
  height: 50%; 
  min-height: 50%;
  position: relative;
  overflow: auto;
  -webkit-animation-delay: 1000ms;
          animation-delay: 1000ms;
}
div#five-day {
  margin: auto;
  width: 95%;
}

p.five-day-forecast {
  margin-top: 10%;
  background: #f1f1f1;
  padding: 3px;
  font-family: "Alegreya" , serif;
  font-weight: 700;
  width: 35%;
  box-shadow: -2.5px -2.5px #ded6d678;
}

p.five-day-desc {
  background: #f1f1f1;
  width: 80%;
  margin-top: -10px;
  box-shadow: -2.5px -2.5px #ded6d678;
}

div#five-day-conditions {
  background: #f1f1f1;
  text-align: center;
  position: relative;
  font-family: "Alegreya" , serif;
  font-weight: 700;
  color: black;
  
}

.five-day-header {
  font-weight: 700;
  margin-bottom: 0;
}

/* Footer */

#footer {
  background-color: grey;
  height: 10%;
  position: relative;
  bottom: 0;
  width: 100%;
  box-shadow: 0 0 3px rgba(0, 0, 0, .2);
  display: flex;
  overflow-x: auto;
}

.footer-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  min-width: 25%;
  overflow: hidden;
  white-space: nowrap;
  font-family: "Alegreya" , serif;
  font-size: 20px;
  color: black;
  -webkit-tap-highlight-color: transparent;
  transition: background-color 0.1s ease-in-out;
}

.footer-link:hover {
  background-color: lightgrey;
  color: #26942e;
}
/* 
.footer-link--active {
  color: #26942e;
} */

.footer-icon {
  font-size: 20px;
}


/* Autofill */

#autofill {
  background-color: #F8F8F8;
  position: absolute;
  box-shadow: 0 1px 3px 0px;
  border-radius: 3px;
  border: 1px solid #FAFAFA;
  z-index: 100;
  max-height: 250px;
  overflow-y: auto;
  list-style: none;
  width: 75%;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

#autofill ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.autofill-li {
  padding: 4px 12px;
  border: 1px solid black;
  width: 100%;
  text-align: center;
}


#autofill ul li:focus,
#autofill ul li:hover {
  background-color: #F0F0F0;
  cursor: pointer;
}

.autofill-li:active {
  transition: background-color 0.4s ease;
  background-color: #378dea;
}

/* Autfill to change link*/

#autofill-changeLink {
  background-color: #F8F8F8;
  position: absolute;
  box-shadow: 0 1px 3px 0px;
  border-radius: 3px;
  border: 1px solid #FAFAFA;
  z-index: 100;
  max-height: 250px;
  overflow-y: auto;
  list-style: none;
  width: 45%;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  margin-left: 51%;
}

#autofill-changeLink ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.autofill-changeLink-li {
  padding: 4px 12px;
  border: 1px solid black;
  width: 100%;
  text-align: center;
}


#autofill-changeLink ul li:focus,
#autofill-changeLink ul li:hover {
  background-color: #F0F0F0;
  cursor: pointer;
}

.autofill-changeLink-li:active {
  transition: background-color 0.4s ease;
  background-color: #378dea;
}

/* Media Query for Desktop*/

@media screen and (min-width: 555px) and (max-width: 1200px) {
  #appTitle {
    margin-bottom: 20px;
  }

  #searchBar {
    width: 70%;
    margin: auto;
  }
  #searchButton {
    margin-top: 10px;
    margin-bottom: -20px;
    width: 30%;
  }

  #favorite-options {
    width: 40%;
    margin-left: 90px;
  }

  input#new-link {
    width: 40%;
    margin-left:600px;
  }

  #nav-container {
    margin-bottom: 10px;
  }
}

/*Media Query for Flex Container */
@media screen and (min-width: 399px) {
  #forecast-flex {
    width: 75%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 525px) {
  #forecast-flex {
    width: 65%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 755px) {
  #forecast-flex {
    width: 55%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 982px) {
  #forecast-flex {
    width: 45%;
    margin-left: auto;
    margin-right: auto;
  }

  #forecast-flex div {
    width: 125px;
    height: 125px;
    font-size: 20px;
  }

  p.five-day-forecast {
    width: 18%;

  }

  p.five-day-desc {
    width: 25%;
  }
  

  ul#autofill {
    margin-top: 90px;
    margin-left: 129px;
    width: 52.5%
  }

  ul#autofill-changeLink {
    margin-left: 600px;
    width: 40%;
  }

}

@media screen and (min-height: 721px) and (min-width: 900px) {
  #appTitle {
    font-size: 3rem;
    height: 10%;
    margin-top: 30px;
  }
  #searchButton {
    margin-bottom: -40px;
  }

  #searchBar {
    margin-top: -60px;
  }

  div.jumbotron {
    height: 42%;
  }
  #footer {
    height: 8%;
  }
}

